<template>
  <div class="collection">
    <topHead
      :formId="formId"
      @deleteClick="deleteClick"
      @nullClick="nullClick"
      @addFormClick="addFormClick"
      @submittedClick="submittedClick"
      @fankuiClick="fankuiClick"
    ></topHead>
    <topSelect :formId="formId" @selectClick="selectClick"></topSelect>
    <div style="border: solid 1px #e0e0e0;margin: 0 10px;">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table
          :data="payInterestTable"
          border
          style="max-width:100%;border: none"
          @selection-change="selsChange"
          :ref="formId+'Table'"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="employmentNumber" label="工号"></el-table-column>
          <el-table-column prop="personName" label="姓名"></el-table-column>
          <el-table-column prop="cardType" label="证照类型"></el-table-column>
          <el-table-column prop="cardNumber" label="证照号码"></el-table-column>
          <el-table-column prop="isBaosong" label="报送状态"></el-table-column>
          <el-table-column prop="isShenfen" label="身份验证状态"></el-table-column>
          <el-table-column prop="address" label="操作">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="updataRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="更新日期">
            <el-input :value="company[0].time_start"></el-input>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!-- 新增和修改表的弹窗 -->
    <el-dialog
      class="in-table"
      title="新增住房贷款支出信息"
      :visible.sync="centerDialogVisible"
      :close-on-click-modal="close_modal"
      width="1000px"
      top="10vh"
    >
      <div class="in-table-content">
        <!-- 表单开始的地方 -->
        <el-form
          :status-icon="true"
          :model="payInterestForm"
          :rules="rules"
          ref="payInterestForm"
          label-width="80px"
          class="demo-ruleForm"
          label-position="right"
          size="mini"
        >
          <!-- 弹窗所有的表格信息 限定为600px高度-->
          <div style="height:600px;padding:0 30px 0 0px; overflow-y:auto">
            <div class="pl-20 mt-5 pb-10 line-bottom">
              <div class="mb-5">
                <span class="content-top-text-blue"></span>
                <span class="title text-red" style="line-height:40px;">温馨提示</span>
              </div>
            </div>
            <div class="ml-50 text-notice">住房租金支出专项附加扣除和房贷利息支出专项附加扣除不得同时享受。</div>

            <!-- 个人信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left:2px">个人信息信息</span>
              </div>

              <div style="display:flex">
                <el-form-item v-if="isUpdata" label="姓名" style="width:33%">
                  <el-input :value="editValue" readonly></el-input>
                </el-form-item>
                <el-form-item v-else label="姓名" style="width:33%" prop="personInfoId">
                  <el-select v-model="payInterestForm.personInfoId" @change="personChange(formId)"
                             placeholder="-请选择姓名-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      :key="i"
                      :label="item.personName"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="证件类型" style="width:33%">
                  <el-input value="居民身份证"></el-input>
                </el-form-item>
                <el-form-item label="证件号码" style="width:33%" prop="cardNumber">
                  <el-select v-model="payInterestForm.cardNumber" placeholder="-请选择证件号码-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      :key="i"
                      :label="item.cardNumber"
                      :value="item.cardNumber"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </div>
            <!-- 配偶信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">配偶信息</span>
              </div>
              <div style="display:flex">
                <el-form-item label="有无配偶" prop="resource">
                  <el-radio-group v-model="payInterestForm.isMate">
                    <el-radio label="有配偶"></el-radio>
                    <el-radio label="无配偶"></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="姓名" style="width:33%">
                  <el-input v-model="payInterestForm.mateName"></el-input>
                </el-form-item>
                <el-form-item label="证照类型" style="width:33%">
                  <el-input v-model="payInterestForm.mateCardType"></el-input>
                </el-form-item>
              </div>
              <el-form-item label="证照号码" style="width:33%">
                <el-input v-model="payInterestForm.mateCardNumber"></el-input>
              </el-form-item>
            </div>
            <!-- 住房信息 -->
            <div class="house-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">住房信息</span>
              </div>
              <div style="display:flex">
                <el-form-item label="房屋坐落地址" label-width="100px" style="width:50%">
                  <el-input v-model="payInterestForm.houseAddress"></el-input>
                </el-form-item>
                <el-form-item
                  label="房屋门牌号"
                  label-width="100px"
                  prop="sitizenship"
                  style="width:50%"
                >
                  <el-input v-model="payInterestForm.houseNumber"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="证书类型" label-width="100px" style="width:50%">
                  <el-select v-model="payInterestForm.certificateStage">
                    <el-option value="房屋所有产权证"></el-option>
                    <el-option value="不动产权证"></el-option>
                    <el-option value="房屋买卖合同"></el-option>
                    <el-option value="房屋预售合同"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item
                  label="房屋证书号"
                  label-width="100px"
                  prop="sitizenship"
                  style="width:50%"
                >
                  <el-input v-model="payInterestForm.certificateNumber"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="本人是否借款" label-width="100px" style="width:50%">
                  <el-radio-group v-model="payInterestForm.isPersonGet">
                    <el-radio label="是"></el-radio>
                    <el-radio label="否"></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label="更新日期" label-width="100px" prop="sitizenship" style="width:50%">
                  <el-input :value="company[0].time_start"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="是否是婚前各自首套贷款，且婚后分别扣除50%" label-width="280px" style="width:50%">
                  <el-radio-group v-model="payInterestForm.isFirstGet">
                    <el-radio label="是"></el-radio>
                    <el-radio label="否"></el-radio>
                  </el-radio-group>
                </el-form-item>

                <el-form-item label="采集来源" prop="sitizenship" label-width="100px" style="width:50%">
                  <el-input value="扣缴义务人"></el-input>
                </el-form-item>
              </div>
            </div>
            <div class="house-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">公积金贷款信息</span>
              </div>
              <div style="display:flex">
                <el-form-item label="贷款合同编号" label-width="100px" style="width:33%">
                  <el-input v-model="payInterestForm.gongLoanNumber"></el-input>
                </el-form-item>
                <el-form-item
                  label="首次还款日期"
                  label-width="100px"
                  prop="sitizenship"
                  style="width:33%"
                >
                  <el-date-picker type="date" v-model="payInterestForm.gongFirstDate" placeholder style="width: 100%;"
                                  value-format="yyyy-MM-dd"></el-date-picker>
                </el-form-item>
                <el-form-item
                  label="贷款期限(月数)"
                  label-width="100px"
                  prop="sitizenship"
                  style="width:33%"
                >
                  <el-input v-model="payInterestForm.gongLoanDeadline"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="贷款银行" label-width="100px" style="width:33%">
                  <el-select v-model="payInterestForm.personName" placeholder="请选择银行">
                    <el-option value="中国工商银行"></el-option>
                    <el-option value="中国银行"></el-option>
                    <el-option value="中国建设银行"></el-option>
                    <el-option value="中国农业银行"></el-option>
                    <el-option value="交通银行"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="扣除有效期起" label-width="100px" style="width:33%">
                  <el-input :value="company[0].time_start" readonly></el-input>
                </el-form-item>
                <el-form-item label="扣除有效期止" label-width="100px" style="width:33%">
                  <el-input :value="company[0].time_end" readonly></el-input>
                </el-form-item>
              </div>
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">商业贷款信息</span>
              </div>
              <div style="display:flex">
                <el-form-item label="贷款合同编号" label-width="100px" style="width:33%">
                  <el-input v-model="payInterestForm.shangLoanNumber"></el-input>
                </el-form-item>
                <el-form-item
                  label="首次还款日期"
                  label-width="100px"
                  prop="sitizenship"
                  style="width:33%"
                >
                  <el-date-picker type="date" v-model="payInterestForm.shangFirstDate" placeholder style="width: 100%;"
                                  value-format="yyyy-MM-dd"></el-date-picker>
                </el-form-item>
                <el-form-item
                  label="贷款期限(月数)"
                  label-width="100px"
                  prop="sitizenship"
                  style="width:33%"
                >
                  <el-input v-model="payInterestForm.shangLoanDeadline"></el-input>
                </el-form-item>
              </div>
              <div style="display:flex">
                <el-form-item label="贷款银行" label-width="100px" style="width:33%">
                  <el-select v-model="payInterestForm.shangBank" placeholder="请选择银行">
                    <el-option value="中国工商银行"></el-option>
                    <el-option value="中国银行"></el-option>
                    <el-option value="中国建设银行"></el-option>
                    <el-option value="中国农业银行"></el-option>
                    <el-option value="交通银行"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="扣除有效期起" label-width="100px" style="width:33%">
                  <el-input :value="company[0].time_start" readonly></el-input>
                </el-form-item>
                <el-form-item label="扣除有效期止" label-width="100px" style="width:33%">
                  <el-input :value="company[0].time_end" readonly></el-input>
                </el-form-item>
              </div>
            </div>
          </div>
          <!-- 弹窗的主题内容结束 -->
          <el-form-item class="btn-right">
            <el-button type="primary" size="small" @click="submitForm('payInterestForm')">保存</el-button>
            <el-button size="small" @click="resetForm('payInterestForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import formHelper from '@/until/formHelper'
import dataHelper from '@/until/dataHelper'
import commonData from '@/until/commonData'
import topHead from './components/top-head'
import topSelect from './components/top-select'
import { mapGetters } from 'vuex'

var tableName = 'payInterestForm'
export default {
  components: { topHead, topSelect },
  data () {
    return {
      ...commonData,
      isIn: true,
      formId: tableName,
      payTableIsEditor: false,
      tableDataOut: [], // 境外人员的表格信息
      centerDialogVisible: false, // 新增和修改表的弹窗
      close_modal: false,
      bodySelect: {
        user: '',
        region: ''
      },
      payInterestForm: {
        cardNumber: '',
        isMate: '',
        mateName: '',
        mateCardType: '居民身份证',
        mateCardNumber: '',
        houseAddress: '',
        houseNumber: '',
        certificateStage: '',
        certificateNumber: '',
        isPersonGet: '',
        isFirstGet: '',
        gongLoanNumber: '',
        gongFirstDate: '',
        gongLoanDeadline: '',
        gongBank: '',
        shangLoanNumber: '',
        shangFirstDate: '',
        shangLoanDeadline: '',
        shangBank: '',
        isBaosong: '未报送',
        isShenfen: '未验证'
      },
      selectCollection: { // 查询的信息
        texPlayerNumber: '',
        employmentNumber: '',
        personName: '',
        cardNumber: '',
        isShenfen: '',
        isBaosong: '',
        porsonStatus: '',
        isCangulie: '',
        employmentType: ''
      },

      rules: {
        cardNumber: [
          { required: true, message: '请选择身份证号', trigger: 'blur' }
        ],
        personInfoId: [
          { required: true, message: '请选择姓名', trigger: 'change' }
        ]
      },
      editValue: ''
    }
  },
  computed: {
    ...mapGetters(['company']),
    personInfo: {
      get () {
        // 获取了名字，身份证号码，身份证类型，数据的主键
        return dataHelper.tableRelevance(this, 'personCollection', ['personName', 'cardNumber', 'cardType', 'infoId'], tableName)
      }
    },
    payInterestTable: {
      get () {
        if (!this.isSelectNow) {
          let b = dataHelper.tableRelevanceArr(this, this.$store.state.form[tableName], this.$store.getters.getFormContent('personCollection'), ['personName', 'cardNumber', 'cardType', 'employmentNumber'], 'personInfoId', 'infoId')
          // return this.$store.getters.getFormContent(tableName)

          return b
          // return []
        } else {
          return this.selectArr
        }
      }
    }
  },
  watch: {
    centerDialogVisible (val) {
      if (!val) {
        this.isUpdata = false
        this[tableName] = this.$options.data()[tableName]
      }
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
  },

  mounted () {
  },
  methods: {
    ...formHelper,
    addFormClick () {
      this.centerDialogVisible = true
    },
    submitForm (formName) {
      var _this = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let submitData = {} // 用于提交的主键，格式参考store
          _this[formName].infoId = _this.$store.getters.getFormInfoId(formName) + 1
          submitData.formId = formName
          submitData.content = _this[formName]
          if (_this.isUpdata) {
            _this.$store.commit('updataFormInfo', submitData)
            _this.isUpdata = false
          } else {
            _this.$store.commit('addFormInfo', submitData)
          }
          this.addMetaInfo(tableName)
          _this.centerDialogVisible = false

          this[formName] = this.$options.data()[formName]
          // alert('提交成功!')
          _this.$message.success('提交成功!')
          console.log(this.$store.getters.getFormContent(tableName))
        } else {
          return false
        }
      })
    },
    updataRow (a, b) {
      this.centerDialogVisible = true
      this[tableName] = this.$store.getters.getFormOneInfo(tableName, b.infoId)
      this.editValue = this[tableName].personName
      this.isUpdata = true
    },
    handleCheckAllChange () {
      this.selectStatus = ['待报送', '待反馈', '报送成功', '报送失败']
    },
    handCancle () {
      this.selectStatus = []
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .collection {
    font-size: 12px;
    text-align: start;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    margin: 0 35px 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 0 0 30px;
  }

  .el-form-item {
    margin-bottom: 5px;
  }
</style>
<style>
  .el-button + .el-button {
    margin: 0;
  }

  .el-dialog__body {
    padding: 0;
  }

  .person-status .el-form-item__label {
    position: relative;
    float: right;
    right: 180px;
  }

  .person-status.el-form-item {
    margin-bottom: 5px;
  }

  .el-form-item {
    margin-bottom: 18px;
  }

  .el-input__inner {
    height: 30px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }

  .top-btn .el-form-item__content {
    position: relative;
    right: 500;
    left: 500;
  }

  .el-form-item.el-form-item--feedback.el-form-item--mini {
    width: 294.69px;
  }

  .el-radio-group {
    position: relative;
    right: -35px;
  }
</style>



